.gallery {
  background: linear-gradient(#fff5f6 0%, #fff5f6 40%, #fff 40%, #fff 100%);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.gallery__thumb img {
  width: 100%;
  height: 100%;
}

.gallery__list {
  margin-left: auto;
  margin-right: auto;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(6, 1fr);
  grid-gap: 4px;
}

@media screen and (min-width: 320px) {
  .gallery__list {
    grid-gap: 8px;
    grid-template-columns: repeat(7, 1fr);
    grid-template-rows: repeat(9, 1fr);
  }
}

.gallery__item {
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.gallery__item:hover {
  transform: scale(1.2);
}

.gallery__item-1 {
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 3;
  grid-row-end: 4;
}

@media screen and (min-width: 320px) {
  .gallery__item-1 {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 4;
    grid-row-end: 7;
  }
}

