.mobile-wrap {
  @media screen and (min-width: $tablet) {
    display: inline-flex;
    align-items: center;
  }
}

.section.section__hero {
  padding: 0;
  background-color: $background-hero;
  height: 665px;
}

.hero__numbers {
  @media screen and (min-width: $tablet) {
    margin-bottom: 72px;
  }
  @media screen and (min-width: $desktop) {
    margin-bottom: 109px;
  }
}

.hero__number-cafes {
  @media screen and (min-width: $tablet) {
    margin-bottom: 13px;
  }
  @media screen and (min-width: $desktop) {
    margin-bottom: 15px;
  }
}

.hero__number {
  @media screen and (min-width: $tablet) {
    z-index: 0;
    position: relative;
    font-family: Titan One;
    font-style: normal;
    font-weight: normal;
    font-size: 28px;
    line-height: 1.14;
    text-align: right;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $text-color-red;
    margin-right: 10px;
    &::before {
      z-index: -1;
      content: '';
      position: absolute;
      width: 16px;
      height: 16px;
      right: -10px;
      top: -3px;
      background: $background-white;
      border-radius: 100%;
    }
  }
  @media screen and (min-width: $desktop) {
    font-size: 46px;
    line-height: 1.15;
    margin-right: 18px;
    &::before {
      width: 30px;
      height: 30px;
      right: -18px;
      top: -4px;
    }
  }
}

.hero__cafes {
  @media screen and (min-width: $tablet) {
    font-family: DM Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 8px;
    line-height: 1.28;
    text-align: right;
    color: $text-color-white;
    margin-right: 10px;
  }
  @media screen and (min-width: $desktop) {
    font-size: 14px;
    line-height: 1.29;
    margin-right: 18px;
  }
}

.hero__girl-img {
  @media screen and (min-width: $tablet) {
    margin-left: auto;
  }
}
